.one .heading{
    width: 100%;
    height: 60vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-image: url(https://wallpaperaccess.com/full/4391662.jpg);
    position: relative;
    background-size: contain;
    object-fit: cover;
    z-index: -1;
}

.heading::before{
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: black;
    opacity: 0.8;
    z-index: -10;
}

.heading h1{
    font-size: 2rem;
    padding: 1rem;
}
