form{
    
    width:auto;
    margin: auto;
    align-items: center;
    display: flex;
    flex-direction: column;
    padding: 20px;
}
.for-border{
    border: solid;
    border-color: black;
    padding: 20px;
    border-radius: 20px;
    background: url("https://wallpapercave.com/wp/wp2288161.jpg");
    background-size: cover;
}

.text{
    font-size: 1.5rem;
    padding: 4px;
   
}

.email{
    font-size: 1.5rem;
    padding: 4px;
}

.message{
    font-size: 1.5rem;
    padding: 4px;
}

.sub{
    font-size: 1.5rem;
    padding: 8px;
}
.sub:hover{
    background-color: rgb(157, 198, 198);
}
