.name{
    justify-content: center;
    width: 100%;

    font-size: 5rem;
    color:hsla(172, 95%, 18%, 1);
    text-align: center;
    margin-top: 10%;
    position: absolute;
}

.picture{
    display: flex;
    flex-direction: column;
    position: absolute;
    width: 100%;
    margin-top: 15%;
}
.container{
    display: flex;
}
.image-container{
   
    width: 30%;
    margin-top: 100px;
    height: auto;
    margin-left: 15%;
    margin-right: 30px;
    display: flex;
}
.image{
    height: 350px;
    margin: auto;
    border-radius: 150px 150px 0 0 ;
   
}

.text-container{
 
    width: 40%;
    margin-top: 100px;
    border: solid;
    border-radius: 10px;
    text-align: justify;
    margin-left: 50px;
}

.second{
    margin-top: 15%;
}
.text-container p{
    color: black;
}


.abtme{
    margin-top: 13.5vh;
    width: 100%;
    height: 92vh;
    background-color: aquamarine;
}
.edu{
    display: flex;
    width: 100%;
    justify-content: space-evenly;
    flex-wrap: wrap;
    column-gap: 10px;
    row-gap: 20px;
    padding: 20px;
}
.edu-head{
    padding: 5px 10px;
}
.eng{
    width:370px;
    
    height: 20vh;
    border-style: solid;
    border-width: 2px;
    border-color: black;
    border-radius: 15px;
}



@media screen and (max-width:850px) {
    .name{
        font-size: 3rem;
        margin-top: 13%;
        transition: 0.5s;
    }

    .image-container{
        width: 40%;
        margin-left: 5%;
    }
    .image{
        height: 250px;
        width: 100%;
        height: auto;
    }
    .text-container{
        margin-top: 150px;
        width: 40%;
        margin-left: 20px;
    }
    .text-container p{
        font-size: 15px;
    }
}
@media screen and (max-width:620px) {
    .text-container p{
        font-size: 13px;
    }
    
}
@media screen and (max-width:550px){
    .name{
        font-size: 2.5rem;
        margin-top: 10vh;
        transition: 0.5s;
    }
    .container{
        flex-direction: column;
    }



    .image-container{
        width: 70%;
        margin-left: 15%;
    }
    .text-container{
        width: 70%;
        margin-left: 15%;
        margin-top: 30px;
       
    }
    

}

@media screen and (max-width:415px){
    .name{
        font-size: 1.5rem;
        
    }

    .picture{
        margin-top: 25%;
    }

    .text-container{
        margin-top: 40px;
    }

}
