.middle{
    display: flex;
    width: 100%;
    height: 40vh;
    margin-top: 20px;
    margin-left: auto;
    justify-content: space-evenly;
    flex-wrap: wrap;
    row-gap: 30px;
    color-scheme: 20px;
    padding-bottom: 1rem;
}

.display{
    width: 400px;
    display: flex;
    flex-direction: column;
    
    align-items: center;
    border: 3px solid;
    border-radius: 15px;

}

.display h1,p{
    color: white;
    padding: 10px;
}

@media screen and (max-width:1220px) {
    .middle{
        height: 80vh;
    }  
    
}
@media screen and (max-width:818px) {
    .middle{
        height: 120vh;
    }  
    .display{
        margin-left: 20px;
        margin-right: 20px;
    }

}
